import React, { CSSProperties, useState, useEffect, Fragment } from "react"
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Backdrop from '@mui/material/Backdrop';
import { Box, TextField, Divider, Select, MenuItem } from "@mui/material";
import Button from '@mui/material/Button'
import LinearProgress from '@mui/material/LinearProgress';
import Grid from "@mui/material/Unstable_Grid2";
import Typography from "@mui/material/Typography";
    
    

function Story() {
  const [loading, setLoading] = useState(false)
  const [loaded, setLoaded] = useState(false)
  const [errMessage, setErrMessage] = useState()
  const [story, setStory] = useState()
  const [characters, setCharacters] = useState("Oscar, Sacha, and a boy named Lucas")
  const [antagonist, setAntagonist] = useState("Motorcycle Monster")
  const [contagonist, setContagonist] = useState("Mister Claw, a clumsy, spooky monster with mustard-colored claws")
  const [language, setLanguage] = useState("English")

  let URL_BASE = ""
  if (process.env.NODE_ENV === 'development') {
    URL_BASE = "http://localhost:8080/story"
  } else {
    URL_BASE = "https://gameserver-iuoqzmbzcq-ew.a.run.app/story"
  }

  // when the story gets updated (with contents from the async call)
  // then we want to finally set loaded=true
  useEffect(() => {
    console.log("in the story.useEffect hook")
    setLoading(false)
    setLoaded(true)
  }, [story])


  function onGenerateClick() {
    setLoading(true)
    getStory();
  }

  function randomizeFields() {
    let tempChar = characters
    let tempAnt = antagonist
    let tempCont = contagonist

    setAntagonist(tempChar)
    setContagonist(tempAnt)
    setCharacters(tempCont)
  }


  return (
    <Grid container spacing={2}>
      <Grid item ><Typography variant="h1">Storyteller</Typography></Grid>
      <Box width="100%"/>
      <Grid item xs={2}>
      <Select
        id="language-select"
        name="Language"
        value={language}
        label="Language"
        onChange={(event) => setLanguage(event.target.value)}
      >
        {["English", "Espaol", "Franais"].map((text, index) => (
          <MenuItem value={text}>{text}</MenuItem>
        ))}
      </Select>
      </Grid>
      <Grid item xs={8}></Grid>
      <Grid item xs={8}>
        <TextField
          required
          fullWidth
          id="characters"
          label="Characters"
          value={characters}
          defaultValue="Oscar, Sacha, and a boy named Lucas"
          onChange={(event) => setCharacters(event.target.value)}
        />
      </Grid>
      <Grid item xs={8}>
        <TextField
          required
          fullWidth
          id="antagonist"
          value={antagonist}
          label="Antagonist"
          defaultValue="Motorcycle Monster"
          onChange={(event) => setAntagonist(event.target.value)}
        />
      </Grid>
      <Grid item xs={8}>
        <TextField
          required
          fullWidth
          value={contagonist}
          id="contagonist"
          label="Contagonist"
          defaultValue="Mister Claw, a clumsy, spooky monster with mustard-colored claws"
          onChange={(event) => setContagonist(event.target.value)}
        />
      </Grid>
      <Box width="100%"/>
      <Grid item >
        <Button variant="contained" onClick={randomizeFields}>Randomize</Button>
      </Grid>
      <Grid item >
        <Button variant="contained" onClick={onGenerateClick}>Go</Button>
      </Grid>
      {loaded ? (
        <Fragment>
          <Backdrop 
            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={loading}
          >
            <Grid item xs={4}>
              <LinearProgress />
            </Grid>
          </Backdrop>
          <Grid item xs={12}>
            <Divider>Story</Divider>
          </Grid>
          <Grid item xs={12}>
            <Typography variant="body1" sx={{ whiteSpace: 'pre-line'}}>{story}</Typography>
          </Grid>
        </Fragment>
      ) : ( <Fragment />) }
    </Grid>
  )


  // fetch a story
  function getStory() {
    let storyURL = `${URL_BASE}?characters=${characters}&contagonist=${contagonist}&antagonist=${antagonist}&language=${language}`
    console.log(`Fetching story from ${storyURL}`);
    fetch(storyURL)
      .then(response => response.json())
      .then(data => {
        // Process the retrieved data
        console.log("FETCHED");
        // setPoints(data);
        setStory(data)
        console.log(data);
      })
      .catch(error => {
        // Handle any errors
        setErrMessage(`Could not retrieve story from ${storyURL}; ${error}`)
        console.error(`Could not retrieve story from ${storyURL}; ${error}`)
        setLoading(false)
        setLoaded(true)
      });
  }
}

export default Story;